import swordpicture from './SwordPicture.png'
import './Download.css';

function Download() {
  return (
    <div className="Download">
		<div className="ImageContent">
			<img src={swordpicture} loading="lazy" alt="Joyeuse game image from jerycho1960 on pixabay" />
			<div className="SpanPosition">
				<span>Joyeuse</span>
			</div>
		</div>
		<div className="PositionMarg">
			<a href="">Download</a>
		</div>
    </div>
  );
}

export default Download;
